@import './dark.scss';

@import './light.scss';
@import url('https://fonts.googleapis.com/css2?family=Rubik+Mono+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.fontRubik {
  font-family: 'Rubik Mono One', sans-serif;
}

.fontRoboto {
  font-family: 'Roboto', sans-serif;
}

.menu-bar {
  // border-bottom: solid 1px #d9d9d9c9;
  box-shadow   : 0px 2px 10px 0px rgba(0, 0, 0, 0.10);
  overflow     : hidden;
  // box-shadow: rgba(4, 17, 29, 0.25) 0px 0px 8px 0px;
  position     : sticky;
  z-index      : 99;
}

.error-dot {
  width           : 8px;
  height          : 8px;
  background-color: #6080B4;
  border-radius   : 50%;
  margin-left     : 12px;
}

// .pink-font {
//   color: #6080B4 !important;
// }

.ant-select-selector {
  background: white;
}

.textmode,
.search-style {
  color: black !important;
}

.ant-menu-sub {
  width: fit-content !important;
}

.ant-modal-header {
  background-color: rgb(255, 255, 255) !important;
}

.center {
  display        : flex !important;
  align-items    : center !important;
  justify-content: center !important;
  color          : #6080B4 !important;
}

.justifyContent {
  display        : flex;
  justify-content: space-between;
}

.logo {
  float : left;
  margin: 0 20px;

  a {
    display: inline-block;
    padding: 12px;

    .logo-info {
      display    : flex;
      align-items: center;
      span {
        font-size  : 22px;
        font-family: HiraginoSansGB, HiraginoSansGB-Regular;
        font-weight: 600;
        text-align : justify;
        color      : rgba(0, 0, 0, 0.80);
        padding-left: 5px;
      }
      img {
        width: 36px;
      }
    }
  }
}

.menuCon {
  flex           : auto;
  width          : 100%;
  display        : flex;
  flex-direction : row;
  flex-wrap      : nowrap;
  justify-content: space-between;
  align-items    : center;
  align-content  : stretch;
}

.menuCon .ant-menu-item {
  padding: 0px 5px;
}

.menuCon .ant-menu-submenu-title {
  padding: 10px 20px;
}

.menuCon .ant-menu-item a,
.menuCon .ant-menu-submenu-title a {
  padding: 10px 15px;
}

.menuCon .ant-menu-horizontal {
  display      : flex;
  align-items  : center;
  border-bottom: none;
}

.menuCon .rightMenu {
  flex       : auto;
  float      : right;
  user-select: none;
}

.menuCon .rightMenu ul.ant-menu {
  width          : 100%;
  float          : right;
  justify-content: flex-end;
}

div.menu-triggerer-container {
  flex        : auto;
  margin-right: 20px;
  display     : none;

  span.anticon.anticon-menu {
    float     : right;
    height    : 32px;
    background: none;
    border    : none !important;
    filter    : drop-shadow(0.35rem 0.35rem 0.4rem rgba(0, 0, 0, 0.5));
    color     : white !important;

    svg {
      font-size: 32px !important;
    }
  }
}

.barsBtn {
  display   : block;
  width     : 20px;
  height    : 2px;
  background: #1890ff;
  position  : relative;
}

.barsBtn:after,
.barsBtn:before {
  content   : attr(x);
  width     : 20px;
  position  : absolute;
  top       : -6px;
  left      : 0;
  height    : 2px;
  background: #1890ff;
}

.barsBtn:after {
  top   : auto;
  bottom: -6px;
}

.ant-drawer-body {
  padding: 0;
}

.ant-drawer-body .ant-menu-horizontal>.ant-menu-item,
.ant-drawer-body .ant-menu-horizontal>.ant-menu-submenu {
  display: inline-block;
  width  : 100%;
}

.ant-drawer-body .ant-menu-horizontal {
  border-bottom: none;
}

.ant-drawer-body .ant-menu-horizontal>.ant-menu-item:hover {
  border-bottom-color: transparent;
}

.text-blur {
  opacity: 0.45;
}

.height-line {
  height: 1.4rem;
}

.flex {
  display    : flex;
  align-items: center;
}

.flex-max {
  flex: 1 1;
}

.ant-select-dropdown {
  border-radius: 0.75rem !important;
}

.icon-tokenpayment {
  width       : 18px;
  height      : 18px;
  margin-right: 0.5rem;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.width-100 {
  width: 100% !important;
}

