.opacity-1 {
  opacity: 1 !important;
}

.opacity-0 {
  opacity: 0 !important;
}

.content {
  position           : relative;
  margin             : 0px;
  padding            : 0px;
  -webkit-box-align  : stretch;
  align-items        : stretch;
  border-width       : 0px;
  border-style       : solid;
  border-color       : rgb(4, 4, 5);
  display            : flex;
  flex               : 1 0 auto;
  flex-direction     : column;
  z-index            : 0;
  -webkit-box-flex   : 1;
  // background-image: url('Assets/backgrounds/bg-submit-nft.png');
  background-position: center;
  background-repeat  : no-repeat;
  background-size    : cover;
}

.bg-header {
  position         : relative;
  margin           : 0px;
  padding          : 0px;
  -webkit-box-align: stretch;
  align-items      : stretch;
  border-width     : 0px;
  border-style     : solid;
  border-color     : rgb(4, 4, 5);
  display          : flex;
  flex-basis       : auto;
  flex-direction   : column;
  flex-shrink      : 0;
  z-index          : 0;
  height           : 75px;
}

.ant-card-bordered {
  border: 1px solid rgba(0, 0, 0, 0.12) !important;
}

.ant-modal-content {
  border-radius: 15px !important;
  padding      : 0.5rem;
}

.ant-modal-content .ant-modal-header {
  border-radius: 15px 15px 0 0 !important;
}

.z-index-2 {
  z-index: 2 !important;
}

.mgb-0 {
  margin-bottom: 0px;
}

.style-sider-left .ant-layout-sider-children .ant-menu-item.ant-menu-item-only-child {
  background: none !important;
}

.mr-1d5rem {
  margin-right: 1.5rem;
}

.mr-0d5rem {
  margin-right: 0.5rem;
}

.bt-cnlo:hover {
  color: #6080B4 !important;
}

.border-none {
  border: none !important;
}

.container {
  width    : 85%;
  margin   : 0 auto;
  max-width: 1500px;
}

.margin-bottom-0 {
  margin-bottom: 0 !important;
}

.radius-1rem {
  border-radius: 1rem !important;
}

.text-color-red {
  color: red !important;
}

.text-color-primary {
  color: #1890ff !important;
}

.ant-layout {
  background: none !important;
}

.events-history {
  display        : flex;
  flex-direction : row;
  flex-wrap      : nowrap;
  justify-content: flex-start;
  align-items    : center;
  align-content  : stretch;
}

.ant-popover-inner {
  border-radius: 1rem !important;
}

.btn-coppy {
  color          : rgb(136, 141, 155);
  flex-shrink    : 0;
  display        : flex;
  text-decoration: none;
  font-size      : 0.825rem;
  border         : none;
  text-decoration: none;
  background     : none !important;
  cursor         : pointer;
  color          : rgb(33, 171, 114);
  font-weight    : 500;
  padding-right  : 0;
}

.icon-coppy {
  margin-left      : 0.25rem;
  font-size        : 0.825rem;
  display          : flex;
  flex-flow        : row nowrap;
  -webkit-box-align: center;
  align-items      : center;
  color            : rgb(34, 34, 34);
}

.row-column {
  display        : flex;
  flex-direction : column;
  flex-wrap      : nowrap;
  justify-content: center;
  align-items    : center;
  align-content  : stretch;
}

.margin-0-auto {
  margin: 0 auto;
}

.link-collection-name {
  color    : #6080B4;
  font-size: 15px;
}

.link-collection-name:hover,
.link-collection-name:active,
.link-collection-name:focus {
  color: #1646e6;
}

.icon-tick {
  margin-left: 0.3rem;
}

